import {
	getWxsh,
} from '@/api/job'
import {
	$wuxToast
} from '../../wxcomponents/wux/index'
export default {
	onLoad() {
		this.getWxsh();
	},
	methods: {
		getWxsh() {
			getWxsh().then(response => {
				let r = response.data
				if (response.code === 20000) {
					const n = parseFloat(r.wxsh)
					if (n === 0) {
						uni.switchTab({
							url: '/pages/index/index'
						});
					}
				}
			})
		},
	}
}
